}

.numberBtn:hover {
  background: #979797;
  color: #a0001e;
  border: 1px solid #a0001e;
}

.numberBtn:active {
  -webkit-transform: scale(1.01);
      -ms-transform: scale(1.01);
          transform: scale(1.01);
}

.bgRed {
  background: #a0001e;
  width: 100%;
  color: white;
}

.wide {
  width: 100%;
}

.narrow {
  font-weight: normal;
}



